@keyframes button {
    0% {
        height: 70px;
    }
    100% {
        height: 90px;
    }
}
@keyframes AnimationRequest1 {
    0% {
        top: 0px;
        left: 0px;
    }

    50% {
         transform:scale(0.3) ;
        left: 0px;
    }

    100% {
        left: 1500px;
        transform:scale(0.3) rotate(45deg);
        
    }
}



.Animation1{
    position: relative;
    animation: AnimationRequest1 ease-in-out 1s 1 backwards;
}





html {
    position: relative;
  
    background-color: rgba(250, 199, 145, 0.885);
}
.container {
    height: 100%;
  .content-menu{
      transform: scale(0.8);
  }
    .Logo {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 625px;
        gap: 18px;
        transition: animation 0.3s;
      

        img {
            height: 130%;
            width: 65%;

            max-width: 828px;

            min-height: 400px;
        }
        img:nth-child(2n) {
            max-width: 228px;
            min-height: 200px;
            margin-bottom: 50px;
        }

        div {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 80px;
            background-color: rgba(222, 184, 135, 0.755);
            filter: saturate(1.8);
            width: 80%;
            border-radius: 400px 400px 0 0;
            height: 60%;
        }

        h1 {
            position: absolute;
            color: rgb(255, 238, 0);
            text-shadow: 4px 1px 1px rgba(0, 0, 0, 0.734);
            font-weight: 700;

            filter: saturate(1.9);
            bottom: 0;
        }
    }

    main {
        display: flex;
        box-sizing: border-box;
        align-items: center;
        justify-content: center;
        position: relative;
      
        margin: auto;
        border: rgb(126, 61, 17) 50px solid;
        background-color: rgb(211, 105, 34);
        background-size: auto;
        border-radius: 360px;
        height: 500px;
        width: 100%;
        padding: 50px;
        margin-bottom: 5%;
       

        

       

       

        span {
            width: 80px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: white;
            font-size: 30px;
            transition: background-color 0.3s;

            cursor: pointer;

            &:hover {
                background-color: #22202077;
            }
        }
    }

    footer {
        background-color: #ff4800;
        color: white;
        font-size: 14px;
        width: 100%;
        position: absolute;

        h2{
            margin-bottom: 50px;
        }
       

        img{
            height: 250px;
        }

    }


    .logo-page-end {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 325px;
     
       

 

        div {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(222, 184, 135, 0.755);
            filter: saturate(1.8);
            width: 80%;
            border-radius: 0 0 400px 400px;
            height: 60%;
            z-index: -1;
        }
    }
    .content-bar {
        height: 200px;

        position: relative;
        align-items: center;
        justify-content: center;
        transition: filter 0.5s;
        margin: auto;
    }
    .content-contacts {
        display: flex;
        background-color: #e55015;
        padding: 10px 0 10px 0;

        color: white;
        text-align: center;
        justify-content: space-evenly;
        align-items: center;
        transition: filter 0.5s;

        a {
            text-decoration: none;
            color: rgb(28, 23, 23);
            font-weight: 500;
            position: relative;
            div {
                display: flex;
                align-items: center;
                justify-content: space-evenly;

                button {
                    border-radius: 50%;
                    width: 40px;
                    height: 40px;
                    overflow: hidden;
                    border-style: hidden;
                    display: flex;
                    flex-direction: row;
                    position: absolute;
                    left: -5px;

                    img {
                        width: 100%;
                        height: 100%;
                        position: relative;
                        cursor: pointer;
                    }
                }

                span {
                    background-color: white;
                    display: flex;
                    align-items: center;
                    height: 40px;

                    visibility: hidden;
                    font-size: 1px;
                    transition: font-size 0.3s;
                    margin-left: 10px;

                    border-bottom-left-radius: 15px;
                    border-top-left-radius: 15px;
                    border-bottom-right-radius: 15px;
                    border-top-right-radius: 15px;
                }

                &:hover {
                    span {
                        font-size: 14px;
                        visibility: visible;
                        padding-left: 45px;
                        padding-right: 45px;
                    }
                }
            }
        }
    }

    .menu-bar {
        margin: auto;
        display: flex;
        padding: 10px;

        align-items: center;
        justify-content: space-around;
        background-color: #e55015c7;
        position: relative;

        width: 90%;
        height: 30%;
        border-radius: 0 0 50px 50px;

        .menu-responsive {
            visibility: hidden;
            display: none;

            width: 120px;
            transition: height 0.2s, background 0.6s;
            span {
                padding: 2px;
                background-color: #e55015;
                border-radius: 15px;
            }
        }

        .content-logo-menu {
            flex: 1;
            height: 60px;

            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            border-radius: 45px;
            margin-right: 100px;
            img {
                flex: 1;
                position: relative;
                top: 25px;
                height: 400%;
            }
        }

        .menu-links {
            flex: 1;
            display: flex;
            align-items: center;
            margin-right: 51px;

            a {
                padding: 0 15px 0 15px;
            }

            span {
                margin: 5px;
                background-color: #e55015;
                border-radius: 15px;
                width: 15px;
            }
        }
        .content-avatar {
            width: 50px;
            z-index: 3;
            border-radius: 50px;
            height: 50px;
            border: rgb(252, 117, 14) 3px solid;
            position: absolute;
            top: 10px;
            right: 5px;
            cursor: pointer;
            overflow: hidden;
            transition: background 0.3s, height 0.3s;
            &:hover {
                height: 105px;
                background-color: #3a3838;
            }

            img {
                width: 50px;

                border-radius: 50px;
            }
            button {
                width: 51px;
                height: 50px;
                cursor: pointer;
                border-radius: 50%;
                background-color: #b41f1f;
                color: white;
                font-weight: 500;
                border: rgb(255, 60, 60) 1px solid;
                transition: filter 0.3s;

                &:hover {
                    filter: saturate(1.5);
                }
            }
        }

        a {
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: space-around;
            border-radius: 45px;
            margin: 5px;
            height: 50px;
            text-decoration: none;
            color: #fff;
            font-weight: 700;
            transition: animation;

            background-color: #3a3838;

            &:hover {
                filter: contrast(1.5);
                animation: button 0.5s 1 forwards;
            }
        }
    }

  
    .ModelRequest {
        display: grid;
        place-items: center;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.624);
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
    }

    .contacts{
        display: flex;
        flex-direction: column;
        margin: auto;
        width: 50%;
        height: 500px;

        div{
            height: 400px;

          
        }
    }
}

@media screen and (max-width: 400px) {
    .container {
        .Logo {
            height: 250px;
            img {
                width: 100%;
                margin-top: 50px;
            }
        }
        .logo-page-end {
            div {
                height: 150px;
            }
        }
    }
}

@media screen and (max-width: 980px) {
    .container {
        .Logo {
    
        }
        .menu-bar {
            display: flex;
            .content-logo-menu {
                flex: 1;
            }

            .menu-links {
                display: none;
                visibility: hidden;
            }

            .menu-responsive {
                display: flex;
                flex-direction: column;
                align-items: center;
                visibility: visible;
                z-index: 2;

                position: absolute;
                right: 30px;
                height: 80px;
                top: 0;

                overflow: hidden;

                &:hover {
                    height: 300px;
                    background-color: rgb(51, 49, 49);
                }

                img {
                    cursor: pointer;
                    position: absolute;
                    top: 15px;
                    width: 36px;
                    height: 50px;
                    margin-bottom: 50px;
                }

                .menu-links-responsive {
                    margin-top: 80px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    a {
                        width: 100%;
                        margin: 0;

                        padding: 10px;
                        border-radius: 0;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 400px) {
    .content-bar {
        .menu-bar {
            .content-logo-menu {
                img {
                    width: 10px;
                }
            }
        }
    }
}
